<template>
  <div class="drop">
    <AppHeaderDropdown right no-caret>
      <template slot="header">
        <span style="margin-right: 20px;"></span>
      </template>
      <template slot="dropdown">
        <b-dropdown-item @click="logoutModal = true"><i class="fa fa-lock"/> </b-dropdown-item>
      </template>
    </AppHeaderDropdown>
    <b-modal title="" class="modal-danger" v-model="logoutModal" @ok="confirm = true" ok-variant="danger">
      
    </b-modal>
  </div>
</template>

<script>
import { HeaderDropdown as AppHeaderDropdown } from '@coreui/vue'
export default {
  name: 'DefaultHeaderDropdownAccnt',
  props: {
    adminInfo: {
      type: String,
      required: true
    }
  },
  components: {
    AppHeaderDropdown
  },
  data: () => {
    return { 
      itemsCount: 5,
      logoutModal: false,
      confirm: false
    }
  },
  methods: {
    logout() {
      if(this.confirm) {
        this.$emit("Logout","logout");
      }
    }
  },
   watch: {
    confirm: {
      handler(newObject,oldObject) {
        // console.log(this.confirm);
        if(this.confirm) {
          this.logout();
        }
      },
      deep: true, //
      immediate: true
    }
  }
}
</script>
